import {inject} from "aurelia-framework";
import {TrackingPixelService} from "./services/tracking-pixel-service";
import TrackingPixelData from "./models/tracking-pixel-data";

@inject(TrackingPixelService)
export class TrackingPixelInformation {
    heading = "Tracking pixel data overzicht";
    trackingPixelData = [];

    constructor(trackingPixelService) {
        this.trackingPixelService = trackingPixelService;
    }

    activate() {
        return this.trackingPixelService.getPagedItems(0)
            .then(response => this.trackingPixelData = Array.from(response, x => new TrackingPixelData(x)));        
    }

    canDeactivate() {

    }
}