import {inject} from "aurelia-framework";
import {Router} from "aurelia-router";
import {SupplierService} from "./services/supplier-service";
import Supplier from "./models/supplier"
import PagedView from "./components/paged-view";

@inject(Router, SupplierService)
export class SuppliersOverview extends PagedView {
    heading = "Overzicht suppliers"
    suppliers = [];
    numberOfPages = 0;  

    constructor(router, supplierService) {
        super(router, "supplier");
        this.supplierService = supplierService;
    }

    activate(params) {
        return super.getPage(params.page || params.pagina || 1);
    }

    getData(page) {
        return this.supplierService.getPagedItems(page)
            .then(response => {
                this.suppliers = Array.from(response.results, x => new Supplier(x));
                this.numberOfPages = response.totalPages;
            });        
    }
}