import {inject} from "aurelia-framework";
import {Router} from "aurelia-router";
import {LeadService} from "./services/lead-service";
import Lead from "./models/lead";
import PagedView from "./components/paged-view";

@inject(Router, LeadService)
export class LeadsOverview extends PagedView {
    heading = "Overzicht inkomende leads"
    leads = [];
    numberOfPages = 0;  

    constructor(router, leadService) {
        super(router, "leads");
        this.leadService = leadService;
    }

    activate(params) {
        return super.getPage(params.page || params.pagina || 1);
    }

    getData(page) {
        return this.leadService.getPagedItems(page)
            .then(response => {
                this.leads = Array.from(response.results, x => new Lead(x));
                this.numberOfPages = response.totalPages;
            });        
    }
}