import {inject} from "aurelia-framework";
import AuthService from "./services/auth-service";

@inject(AuthService)
export class Login {
    username = "";
    password = "";
    rememberMe = false;
    error = false;
    busy = false;

    constructor(auth) {
        this.auth = auth;
    }

    activate() {
        this.clearFields();
    }

    login() {
        if (this.username && this.password) {
            this.error = false;
            this.busy = true;

            let data = $("#loginForm").serialize();

            this.auth.logIn({username:this.username, password:this.password}, this.rememberMe).catch(response => {
                this.error = true;
                this.busy = false;
            });
        }
    }

    clearFields() {
        this.username = "";
        this.password = "";
        this.rememberMe = false;
        this.error = false;
        this.busy = false;       
    }
}