import {inject} from "aurelia-framework";
import {Router} from "aurelia-router";
import {CampaignService} from "./services/campaign-service";
import Campaign from "./models/campaign"
import PagedView from "./components/paged-view";

@inject(Router, CampaignService)
export class CampaignsOverview extends PagedView {
    heading = "Overzicht campagnes"
    campaigns = [];
    numberOfPages = 0;  

    constructor(router, campaignService) {
        super(router, "campaigns");
        this.campaignService = campaignService;
    }

    activate(params) {
        return super.getPage(params.page || params.pagina || 1);
    }

    getData(page) {
        return this.campaignService.getPagedItems(page)
            .then(response => {
                this.campaigns = Array.from(response.results, x => new Campaign(x));
                this.numberOfPages = response.totalPages;
            });        
    }
}