import {inject} from "aurelia-framework";
import {Router} from "aurelia-router";
import {ProjectService} from "./services/project-service";
import Project from "./models/project"
import PagedView from "./components/paged-view";

@inject(Router, ProjectService)
export class ProjectsOverview extends PagedView {
    heading = "Overzicht projecten"
    projects = [];
    numberOfPages = 0;  

    constructor(router, projectService) {
        super(router, "projects");
        this.projectService = projectService;
    }

    activate(params) {
        return super.getPage(params.page || params.pagina || 1);
    }

    getData(page) {
        return this.projectService.getPagedItems(page)
            .then(response => {
                this.projects = Array.from(response.results, x => new Project(x));
                this.numberOfPages = response.totalPages;
            });        
    }
}